.Layout_2uvSR {
  display: flex;
  flex-direction: column;
  height: 100%;
  font-family: PingFangSC-Regular;
}
.Layout_2uvSR .head {
  position: relative;
}
.Layout_2uvSR .content {
  flex: auto;
  box-sizing: border-box;
  background: #fafafa;
}
.Layout_2uvSR .foot {
  padding: 42px 0 10px;
  text-align: center;
  background: #1E1D1F;
  font-size: 14px;
  color: #BEBEBE;
  letter-spacing: 0.6px;
}
.Layout_2uvSR .user-info {
  float: right;
  margin: 15px 30px 72px 18.75%;
  position: relative;
  z-index: 1;
}
.Layout_2uvSR .site {
  position: relative;
  width: 30%;
  padding: 18px 0 14px 10%;
  z-index: 3;
}
.Layout_2uvSR .site .name {
  font-size: 24px;
  line-height: 24px;
  color: #333333;
  letter-spacing: 0.75px;
}
.Layout_2uvSR .site .address {
  margin-top: 4px;
  font-size: 20px;
  color: #999999;
  letter-spacing: 0.62px;
  line-height: 20px;
}
.Layout_2uvSR .site a {
  text-decoration: none;
  color: inherit;
}
.Layout_2uvSR .site a:hover {
  color: yellowgreen;
  text-decoration: gainsboro;
}
.Layout_2uvSR .layout-menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: flex-end;
  padding: 32px 30px 32px 0;
  font-weight: 500;
  box-shadow: 0 0 1px #666666;
}
.Layout_2uvSR .layout-menu li {
  font-size: 18px;
  line-height: 16px;
  color: #666666;
  letter-spacing: 1.12px;
  margin-right: 70px;
}
.Layout_2uvSR .layout-menu a {
  position: relative;
  color: #666666;
  text-decoration: none;
}
.Layout_2uvSR .layout-menu a.selected,
.Layout_2uvSR .layout-menu a:hover {
  color: #181717;
  font-weight: 500;
}
.Layout_2uvSR .layout-menu a.selected::after,
.Layout_2uvSR .layout-menu a:hover::after {
  position: absolute;
  bottom: -12px;
  left: 0;
  right: 0;
  content: '';
  transition: all 0.5s ease-in;
  border-bottom: 3px solid #FFD338;
}
